<template>
  <div class="r-task-list">
    <task-item v-for="(item, index) in dataList" :key="index" :task-item="item" :state="state"
      @download="handleDownload" @doDeal="handleDeal" @doView="handleView">
    </task-item>
  </div>
</template>

<script>
import TaskItem from "./r-task-item";
export default {
  name: "task-list",
  components: { TaskItem },
  props: {
    dataList: {
      type: Array,
      default: () => {
        return [];
      }
    },
    state: {
      type: String,
      default: "0"
    }
  },
  data() {
    return {};
  },
  methods: {
    handleDownload(item) {
      this.$emit("download", item);
    },
    handleDeal(item) {
      this.$emit("doDeal", item);
    },
    handleView(item) {
      this.$emit("doView", item);
    }
  }
};
</script>



