<template>
  <div id="home-page">
    <head-top title="我的首页" :show-back="false"></head-top>
    <div class="bgshouy"></div>

    <div class="head_nav">
      <div v-for="(item, index) in navList" :key="index" class="nav_item" @click="jump(index)">
        <i class="iconfont" :class="[item.icon, item.colorName]"></i>
        <div>{{ item.name }}</div>
      </div>
    </div>

    <div class="sub_nav" @click="$router.push('/space/homework')">
      <span>近期任务</span>
      <div type="text" style="font-weight: normal;">查看全部</div>
    </div>

    <task-list v-if="practiceList && practiceList.length > 0" :data-list="practiceList"
      @download="download" @doDeal="doPractice">
    </task-list>
    <task-list v-if="courseWareList && courseWareList.length > 0" :data-list="courseWareList"
      :style="{paddingTop:'0'}" @download="download" @doDeal="doPractice">
    </task-list>
    <r-lack-data v-else-if="!practiceList || practiceList.length === 0">
    </r-lack-data>

    <div class="sub_nav" @click="$router.push('/space/note')">
      <span>近期笔记</span>
      <div type="text" style="font-weight: normal;">查看全部</div>
    </div>
    <note-list v-if="noteDataList && noteDataList.length > 0" :data-list="noteDataList"
      :style="{paddingTop: '0'}" @select="handleSelectNote">
    </note-list>

  </div>
</template>

<script>
import TaskList from "@/components/r-task-list";
import NoteList from "@/components/r-note";
import { getNowDate, getSetDate, transformValue } from "@/utils/index";

export default {
  name: "",
  components: { TaskList, NoteList },
  props: {},
  data() {
    return {
      navList: [
        {
          icon: "icon-fuxi",
          colorName: "nav_1",
          name: "课程",
          path: "/homeCourse"
        },
        {
          icon: "icon-zuoye",
          colorName: "nav_2",
          name: "任务",
          path: "/space/homework"
        },
        {
          icon: "icon-edu-biji",
          colorName: "nav_3",
          name: "笔记",
          path: "/space/note"
        }
      ],
      practiceList: [],
      courseWareList: [],
      today: getNowDate(),
      noteDataList: []
    };
  },
  computed: {},
  watch: {},
  created() {
    this.getPracticeTask();
    this.getCourseWareTask();
    this.getNoteList();
  },
  mounted() {},
  methods: {
    jump(index) {
      if (this.navList[index].path) {
        this.$router.push(this.navList[index].path);
        return;
      }
      let jump = document.getElementsByClassName("sub_nav");
      // 获取需要滚动的距离
      let total = jump[index - 1].offsetTop;
      const currentY =
        document.documentElement.scrollTop || document.body.scrollTop;
      this.scrollAnimation(currentY, total);
    },
    scrollAnimation(currentY, targetY) {
      // 计算需要移动的距离
      let needScrollTop = targetY - currentY;
      let _currentY = currentY;
      setTimeout(() => {
        // 一次调用滑动帧数，每次调用会不一样
        const dist = Math.ceil(needScrollTop / 10);
        _currentY += dist;
        window.scrollTo(_currentY, currentY);
        // 如果移动幅度小于十个像素，直接移动，否则递归调用，实现动画效果
        if (needScrollTop > 10 || needScrollTop < -10) {
          this.scrollAnimation(_currentY, targetY);
        } else {
          window.scrollTo(_currentY, targetY);
        }
      }, 1);
    },

    getPracticeTask() {
      this.practiceList = [];
      let reqData = {
        pno: 1,
        psize: 2,
        estate: 1,
        stuID: this.$store.getters.studentID
      };
      this.$mainHttp.classHour
        .getPracticeTaskList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.practiceList.push(item);
              });
            }
          }
        })
        .catch(() => {});
    },
    getCourseWareTask() {
      this.courseWareList = [];
      let reqData = {
        pno: 1,
        psize: 2,
        estate: 1,
        stuID: this.$store.getters.studentID
      };
      this.$mainHttp.classHour
        .getCourseWareTaskList(reqData)
        .then(res => {
          if (res.Result) {
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.courseWareList.push(item);
              });
            }
          }
        })
        .catch(() => {});
    },

    getNoteList() {
      let reqData = {
        startDate: getSetDate("date", -365 * 10),
        endDate: this.today,
        psize: 3,
        pno: 1,
        snid: "",
        stuid: this.$store.getters.studentID,
        estate: 1,
        key: ""
      };
      this.$mainHttp.note
        .getSearchList(reqData)
        .then(res => {
          if (res.Result && res.Datas.length > 0) {
            res.Datas.forEach(item => {
              // this.noteList.push(item);
              this.noteDataList.push({
                id: item.Key["SNID"],
                title: item.Key["SNTtile"],
                content: item.Key["SNContent"],
                state: item.Key["State"],
                createTime: item.Key["CreateTime"],
                CHID: item.Key["CHID"],
                CIID: item.Key["CIID"],
                CHName: item["CHName"],
                CourseName: item["CourseName"]
              });
            });
          }
        })
        .catch(err => {});
    }, // 图片路径转换
    transformValue(value) {
      return transformValue("show", value, this.$mainHttp.resourse.picture);
    },
    download(item) {
      let fileUrl = this.$mainHttp.resourse.file(item["MaterialID"]);
      if (!window.webkit) {
        window.location.href = fileUrl;
        this.addCoursewareTask(item);
      } else {
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {
            this.addCoursewareTask(item);
          })
          .catch(err => {});
      }
    },
    addCoursewareTask(item) {
      this.$mainHttp.courseware
        .postCoursewareTask({
          cID: item["CID"],
          stuID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result) {
            this.$toast("课件下载成功！");
            this.getCourseWareTask();
          }
        })
        .catch(err => {});
    },
    doPractice(item) {
      this.$router.push(
        `/space/practiceDone/${item["CIPID"]}/${item["PLID"]}/${item["CPID"]}/${item["CHID"]}`
      );
    },
    handleSelectNote(item) {
      this.$router.push({
        path: "/space/noteView",
        query: {
          snID: item.id,
          courseID: item["CIID"],
          courseName: item["CourseName"],
          classHourID: item["CHID"],
          classHourName: item["CHName"],
          title: item.title,
          content: item.content,
          createTime: item.createTime
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@font-title: 1.25rem;
@font-sub-title: 1rem;
@font-info: 1rem;
#home-page {
  width: 100%;
  min-height: calc(100% - 4.3rem);
  padding-bottom: 4.3rem;
  background-color: #fff;
  .bgshouy {
    background: url("../assets/bg_banner.png") 100% 100% e("/") 100% 100%;
    width: 100vw;
    height: 17vh;
    // background-size: cover;
    margin-bottom: 6vw;
  }

  .head_nav {
    display: flex;
    justify-content: space-around;
    align-items: center;

    .nav_item {
      padding: 2vw;
      font-size: 1.5 * @font-sub-title;
      text-align: center;

      i {
        font-size: 2 * @font-sub-title;
        padding: 1.25rem;
        color: #fff;
        border-radius: 50%;
      }

      div {
        padding-top: 1.5rem;
      }

      .nav_1 {
        background-color: #00cbab;
      }

      .nav_2 {
        background-color: #51bef1;
      }

      .nav_3 {
        background-color: #f49f4d;
      }

      .nav_4 {
        background-color: #a36ee5;
      }
    }
  }

  .sub_nav {
    position: relative;
    margin-top: 1rem;
    padding: 0 5vw;
    display: flex;
    justify-content: space-between;
    background-color: #f6f7fa;

    font-size: @font-title;
    font-weight: bolder;
    line-height: 3.125rem;

    &:before {
      content: " ";
      position: absolute;
      top: 50%;
      left: 2vw;
      width: 1.5vw;
      height: 1.5rem;
      transform: translateY(-50%);
      background-color: #00cbab;
    }
  }
}
</style>



