<template>
  <div class="r-task-item">
    <div class="item-title">
      <h3> {{ taskItem['Title'] }} </h3>
      <div>
        <div v-if="taskItem['Type']===1" size="mini">
          <van-icon class-prefix="iconfont icon-xiazai" @click.stop.prevent="handleDownload">
          </van-icon>
        </div>
        <van-button v-else-if="state==='0'" size="mini" type="primary" @click="handleDeal">
          去完成
        </van-button>
        <van-button v-else size="mini" type="primary" @click="handleView">
          查看
        </van-button>
      </div>
    </div>
    <div class="item-foot foot-remark">
      <span>
        {{ taskItem["Attribute"] === '1' ? "课前" : "课后" }}
      </span>
      <span>
        {{taskItem['Type']===1?'课件':'随堂练习'}}
      </span>
      <span>
        {{ taskItem['CreateTime'].split(" ")[0] }}
      </span>
    </div>
    <div class="item-foot foot-info">
      <span>
        {{ taskItem['CHName'] }}
      </span>
      <span>
        老师：{{ taskItem['TeacherName'] }}
      </span>
    </div>

  </div>
</template>

<script>
export default {
  name: "task-item",
  props: {
    taskItem: {
      type: Object
    },
    state: {
      type: String,
      default: "0" // 0未完成 1已完成
    }
  },
  data() {
    return {};
  },
  methods: {
    handleDownload() {
      this.$emit("download", this.taskItem);
    },
    handleDeal() {
      this.$emit("doDeal", this.taskItem);
    },
    handleView() {
      this.$emit("doView", this.taskItem);
    }
  }
};
</script>


